<template>
  <InfoCart
    :pers="arrItemsApi"
    :locationinfo = 'locatoin[0]'
    v-if="hiddenBloc"
    @close-window="hiddenBloc = false"
  />
  <div class="cart" @click="hiddenBloc = true">
    <img :src="srtimg" alt="title" />
    <h3>
      –ò–º—è: <strong>{{ name }}</strong>
    </h3>
  </div>
</template>

<script>
import InfoCart from "./InfoCart";
export default {
  components: {
    InfoCart,
  },
  mounted(){
          this.Apiitem(this.locationUrl,this.locatoin)
     
  },
  data() {
    return {
      name: this.arrItemsApi.name,
      srtimg: this.arrItemsApi.image,
      hiddenBloc: false,
      locatoin:[],
      locationUrl: this.arrItemsApi.location.url,
    };
  },
  props: ["arrItemsApi"],
  methods: {
    Apiitem(url, arr, id = "") {
      fetch(url + id, {
        method: "GET",
      })
        .then((resp) => resp.json())
        .then((res) => {
          arr.push(res);
       
        });
        
    },
  },
};
</script>

<style scoped>
.cart {
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  width: 200px;
  margin: 10px;
  box-shadow: rgba(0, 0, 0, 0.226) 0 0 22px;
  border-radius: 15px;
  overflow: hidden;
}
img {
  width: 100%;
}
@media screen and (max-width: 800px) {
  .cart {
    width: 300px;
  }
}
</style>
