<template>
  <div
    class="stop"
    @mousemove.stop="cursortrek"
    :style="{
      left: xt + 'px',
      top: yt + 'px',
    }"
  >
    <div class="infocart" ref="divA" @mouseup.right="clickInfocart"  @contextmenu.prevent>
      <div class="close" @mouseup="closeWindow">‚ùå</div>
      <div class="pers"   >
        <img :src="srs" :alt="name" />
        <div class="persimfo" v-if="showadd==false">
          <h2>–ò–º—è: {{ name }}</h2>
          <h2>Gender: {{ gender }}</h2>
          <h2>–†–∞—Å–∞: {{ species }}</h2>
          <h2>–ù–∞ –¥–∞–Ω—ã–π –º–æ–º–µ–Ω—Ç: {{ status }}</h2>
        </div>
         
      </div>
      <div class="persimfo" v-if="showadd">
          <h2>–ë—ã–ª —É—á–∞—Å—Ç–Ω–∏–∫–æ–º –≤ {{ pers.episode.length }} —ç–ø–∏–∑–æ–¥–µ</h2>
          <h2>–ù–∞—Ö–æ–¥–∏—Ç—Å—è –≤ –∏–∑–º–µ—Ä–µ–Ω–∏–∏: {{ locationinfo.dimension }}</h2>
          <h2>–¢–∏–ø: {{ locationinfo.type }}</h2>
          <h2>–ù–∞–∑–≤–∞–Ω–∏–µ: {{ locationinfo.name }}</h2>
        </div>
      <div class="pers2" v-if="!showadd">
        <h2>–ú–µ—Å—Ç–æ–ø–æ–ª–æ–∂–µ–Ω–∏–µ: {{ location }}</h2>
      </div>
  
      <a class="info2" @click.stop="showadd = !showadd ;this.mouse=false ">
        –µ—â—ë –±–æ–ª—å—à–µ –∏–Ω—Ñ–æ—Ä–º–∞—Ü–∏–∏
      </a>
    </div>
  </div>
</template>

<script>
export default {
  functional: true,
  props: ["pers", "locationinfo"],
  emits: ["close-window"],
  data() {
    return {
      showadd: false,
      ww: window.innerWidth,
      mouse: false,
      name: this.pers.name,
      gender: this.pers.gender,
      species: this.pers.species,
      status: this.pers.status,
      srs: this.pers.image,
      location: this.pers.location.name,
      x: 0,
      y: 0,
      xt: 0,
      yt: 0,
    };
  },
  mounted() {
    this.xt = window.innerWidth / 2;
    this.yt = window.innerHeight / 2;
  },
  methods: {
    closeWindow() {
      this.$emit("close-window");
    },
    clickInfocart(e) {
      const x = this.xt - this.x;
      const y = this.yt - this.y;
      this.x = x - e.x - 15;
      this.y = y - e.y - 15;
      this.mouse = !this.mouse;
    },
    cursortrek(e) {
      if (this.mouse && this.ww > 700) {
        this.xt = e.x + this.x;
        this.yt = e.y + this.y;
      }
    },
  },
  watch: {},
};
</script>

<style scoped>
