  outline: none;
  background-color: rgb(255, 255, 255);
  box-shadow: rgba(0, 0, 0, 0.226) 0 0 22px;
  border-radius: 15px;
  overflow: hidden;
  -webkit-user-select: none;
  -khtml-user-select: none;
